<template>
 <v-container>
      <v-card color="red darken-4" style="width:300px;">
        <v-carousel style="width:300px;" height="500">
          <v-carousel-item
            v-for="movie in order.movies"
            :key="movie.key"
            :src="movie.Poster"
          ></v-carousel-item>
        </v-carousel>
        <v-card-title primary-title>
          <div class="subheading">
            <small>Order Id: {{order.key}}</small><br>
            <small>Time: {{order.time}}</small>
            <h3>Order Total: ${{order.totalPrice}}</h3>
          </div>
        </v-card-title>
      </v-card>
 </v-container>
</template>

<script>
    export default {
        name: "OrderTile",
        data() {
          return {
            color: "dark"
          }
        },
        props:{
          order: Object,
        },
      methods:{
      }
    }

</script>

<style scoped>

</style>



